	module.exports = {"data":{"markdownRemark":{"html":"<h2>Redux Actions</h2>\n<p>Redux action is a simple object that describes what sort of event has happened in your application. They can even contain\ndata that needs to be send from the application to the Redux store. Action can contain anything but it must have a mandatory\ntype property which describes the event taking place. A good practice is to use constants while describing the action.</p>\n<p>For example</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">const</span> ADD_ITEM <span class=\"token operator\">=</span> <span class=\"token string\">'ADD_ITEM'</span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">{</span>\n type<span class=\"token punctuation\">:</span> ADD_ITEM<span class=\"token punctuation\">,</span>\n text<span class=\"token punctuation\">:</span> <span class=\"token string\">'This is the first item'</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>We can send these actions to the store by using </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>store<span class=\"token punctuation\">.</span><span class=\"token function\">dispatch</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p>An application can have different sorts of events happening at a time and these actions helps describe these events. Without these\nactions there is no way to change the state of the application. </p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://redux.js.org/basics/actions\">Actions-Redux Offical Docs</a></p>","fields":{"slug":"/redux/redux-actions/"},"frontmatter":{"title":"Redux Actions"}}},"pathContext":{"slug":"/redux/redux-actions/"}}

